.contactForm__container {
  padding: 0;
  margin: 0;
  width: 100%;
  height: auto;
}

.contactForm__container-inputs {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
  -ms-flex-direction: column;
  flex-direction: column;
  -webkit-box-pack: justify;
  -ms-flex-pack: justify;
  justify-content: space-between;
}

.contactForm__container-inputs input {
  margin-bottom: 2em;
  height: 30%;
  border: none;
  border-bottom: 4px dashed var(--color-heading);
  background-color: var(--color-bg);
}
.contactForm__container-inputs input:focus,
input.form-control:focus {
  outline: none !important;
  outline-width: 0 !important;
  box-shadow: none;
  -moz-box-shadow: none;
  -webkit-box-shadow: none;
}
.contactForm__container-inputs textarea:focus,
textarea.form-control:focus {
  outline: none !important;
  outline-width: 0 !important;
  box-shadow: none;
  -moz-box-shadow: none;
  -webkit-box-shadow: none;
}

.contactForm__container-inputs label {
  font-family: var(--font-family-text);
  font-size: 1rem;
  font-weight: lighter;
  color: var(--color-text);
}

.contactForm__container-inputs textarea {
  margin: 2em 0;
  border: 4px dashed var(--color-heading);
  height: 150px;
  background-color: var(--color-bg);
}

.contactForm__container-inputs button {
  width: 40%;
  padding: 15px 40px;
  border-radius: 10px;
  border: none;
  background-color: var(--color-heading);
  margin: 0 210px;

  font-family: var(--font-family-text);
  font-size: 1em;
  color: var(--color-text);
}
@media screen and (max-width: 768px) {
  .contactForm__container-inputs button {
    margin: 0;
    width: 100%;
  }
}
@media screen and (max-width: 480px) {
  .contactForm__container-inputs button {
    margin: 0;
    width: 100%;
  }
}
@media screen and (max-width: 320px) {
  .contactForm__container-inputs button {
    margin: 0;
    width: 100%;
  }
}
