.project-container {
  margin: 1em 2em;
}

.section__heading {
  width: 100%;
  height: auto;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
  -ms-flex-pack: center;
  justify-content: center;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;

  padding: 20px 0;
  margin: 20px 0;
}

.section__heading h2 {
  font-family: var(--font-family-heading);
  font-size: 128px;
  font-weight: bolder;
  color: var(--color-heading);
}

