.member__container {
  display: -ms-grid;
  display: grid;
  gap: 20px;
  -ms-grid-columns: 1fr 20px 1fr;
  grid-template-columns: 1fr 1fr;
  margin-bottom: 30px;
}
.member__image-container {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
  -ms-flex-pack: center;
  justify-content: center;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: flex-start;
  width: 100%;
  height: 70%; /* Ensure it scales based on width */
  border-radius: 10%;
  border: 3px solid var(--color-heading);
  overflow: hidden;
}
.member__image-container img {
  width: 100%;
  height: 100%;
  max-width: 100%;
  -o-object-fit: cover;
  object-fit: cover; /* Ensure the entire image fits within the container */
  aspect-ratio: 1;
}

.member__info-container {
  padding: 20px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
  -ms-flex-direction: column;
  flex-direction: column;
  -webkit-box-pack: flex-start;
  -ms-flex-pack: flex-start;
  justify-content: flex-start;
  -webkit-box-align: end;
  -ms-flex-align: end;
  align-items: flex-end;
}

.member__info-container_h2 {
  width: auto;
  height: 8%;
}
.member__info-container_h2 h2 {
  margin: 0;

  width: auto;

  font-family: var(--font-family-text);
  font-size: 65px;
  font-weight: bold;
  color: var(--color-heading);
}

.member__info-container_h3 h3 {
  margin: 0;

  font-family: var(--font-family-text);
  font-size: 40px;
  font-weight: lighter;
  color: var(--color-heading);
}
.member__info-container-description {
  width: 67%;
}
.member__info-container-description p {
  font-family: var(--font-family-text);
  font-size: 1.2rem;
  line-height: 25pt;
  font-weight: normal;
  text-align: justify;
  color: var(--color-text);
}

@media screen and (max-width: 768px) {
  .member__container {
    display: block;
  }
  .member__info-container_h2,
  .member__info-container_h3,
  .member__info-container-description {
    width: 100%;
  }
  .member__info-container_h2 h2 {
    font-size: 45px;
    text-align: center;
  }
  .member__info-container_h3 h3 {
    font-size: 25px;
    text-align: center;
  }

  .member__info-container-description p {
    font-size: 14px;
  }
}
@media screen and (max-width: 480px) {
  .member__container {
    display: block;
  }
  .member__info-container_h2,
  .member__info-container_h3,
  .member__info-container-description {
    width: 100%;
  }
  .member__info-container_h2 h2 {
    font-size: 45px;
    text-align: center;
  }
  .member__info-container_h3 h3 {
    font-size: 25px;
    text-align: center;
  }

  .member__info-container-description p {
    font-size: 14px;
  }
}
@media screen and (max-width: 320px) {
  .member__container {
    display: block;
  }
  .member__info-container_h2,
  .member__info-container_h3,
  .member__info-container-description {
    width: 100%;
  }
  .member__info-container_h2 h2 {
    font-size: 45px;
    text-align: center;
  }
  .member__info-container_h3 h3 {
    font-size: 25px;
    text-align: center;
  }

  .member__info-container-description p {
    font-size: 14px;
  }
}
