.contact {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
  -ms-flex-pack: center;
  justify-content: center;
  width: 100%;
  margin-top: 50px;
  padding: 2em;
}

.contact__container {
  width: 76%;
  margin: 20px;
  padding: 20px;
  border: 5px solid var(--color-heading);
}

.contact__container-heading {
  position: relative;
  z-index: 1;
  left: 30px;
  top: -220px;
}
.contact__container-grid {
  display: -ms-grid;
  display: grid;
  gap: 20px;
  -ms-grid-columns: 1fr 20px 1fr;
  grid-template-columns: 1fr 1fr;
}

.contact__container-grid_form {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
  -ms-flex-pack: center;
  justify-content: center;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
  padding: 20px;
}

.contact__container-info {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
  -ms-flex-direction: column;
  flex-direction: column;
  -webkit-box-pack: justify;
  -ms-flex-pack: justify;
  justify-content: space-between;
  -webkit-box-align: start;
  -ms-flex-align: start;
  align-items: flex-start;
  padding: 0 30px;
}
.contact__container-info-socials {
  padding: 0 20%;
}

@media screen and (max-width: 768px) {
  .contact {
    margin-top: 50px;
    padding: 20px;
  }
  .contact__container {
    margin: 0px;
    width: 100%;
  }
  .contact__container-heading {
    left: 25px;
    top: -80px;
  }

  .contact__container-grid {
    display: flex;
    flex-direction: column-reverse;
  }
  .contact__container-info {
    padding: 0px;
  }
  .contact__container-grid_form {
    padding: 0px;
  }
  .contact__container-info-socials {
    width: 100%;
    padding: 0;
    margin-top: 45px;
  }
}
@media screen and (max-width: 480px) {
  .contact {
    margin-top: 50px;
    padding: 20px;
  }
  .contact__container {
    margin: 0px;
    width: 100%;
  }
  .contact__container-heading {
    left: 10px;
    top: -60px;
  }

  .contact__container-grid {
    display: flex;
    flex-direction: column-reverse;
  }
  .contact__container-info {
    padding: 0px;
  }
  .contact__container-grid_form {
    padding: 0px;
  }
  .contact__container-info-socials {
    width: 100%;
    padding: 0;
    margin-top: 20px;
  }
}
@media screen and (max-width: 320px) {
  .contact {
    margin-top: 50px;
    padding: 20px;
  }
  .contact__container {
    margin: 0px;
    width: 100%;
  }
  .contact__container-heading {
    left: 10px;
    top: -53px;
  }

  .contact__container-grid {
    display: flex;
    flex-direction: column-reverse;
  }
  .contact__container-info {
    padding: 0px;
  }
  .contact__container-grid_form {
    padding: 0px;
  }
  .contact__container-info-socials {
    width: 100%;
    padding: 0;
    margin-top: 20px;
  }
}
