.footer {
  width: 100%;
  height: auto;

  margin-top: 30px;
}

.footer__container {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
  -ms-flex-direction: column;
  flex-direction: column;
  -ms-flex-pack: distribute;
  justify-content: space-around;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
  background-color: var(--color-footer);
}

.footer__container-heading h3 {
  margin: 20px 0;
  font-family: var(--font-family-heading);
  font-size: 80px;
  font-weight: bold;
  color: var(--color-heading);
  letter-spacing: 10pt;
}

.footer__container-navLinks {
  margin: 20px 0;
  width: 35%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
  -ms-flex-pack: justify;
  justify-content: space-between;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
}

.footer__container-navLinks a {
  font-family: var(--font-family-text);
  font-size: 18px;
  font-weight: normal;
  color: var(--color-text);
}

.footer__container-contactDetails {
  margin: 30px 0;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
  -ms-flex-direction: row;
  flex-direction: row;
  -webkit-box-pack: justify;
  -ms-flex-pack: justify;
  justify-content: space-between;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
}
.footer__container-contactDetails {
  position: relative;
  left: -1%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
  -ms-flex-direction: row;
  flex-direction: row;
  -webkit-box-pack: justify;
  -ms-flex-pack: justify;
  justify-content: space-between;
  -webkit-box-align: flex-start;
  -ms-flex-align: flex-start;
  align-items: flex-start;
}

.footer__container-contactDetails_grid {
  margin: 0 30px;
  display: -ms-grid;
  display: grid;
  gap: 20px;
  -ms-grid-rows: auto 10px auto;
  grid-template-rows: auto auto; /* Ensure two rows with automatic height */
  margin-bottom: 20px;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center; /* Center content vertically */
}

.footer__contactDetails-img {
  width: auto;
  height: auto;
  padding: 10px 40px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
  -ms-flex-pack: center;
  justify-content: center;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
}

.footer__contactDetails-text {
  padding: 10px 40px;
  width: auto;
  font-family: var(--font-family-text);
  font-size: 16px;
  font-weight: normal;
  color: var(--color-text);
}

.footer__contactDetails-text p {
  text-align: justify;
  text-align: justify;
  max-width: 200px; /* Adjust the width as needed to ensure three lines */
  word-wrap: break-word;
}
.footer__container-socials {
  width: 100%;
  height: auto;
  padding: 0;
  margin: 30px 0;
}

@media screen and (max-width: 768px) {
}
@media screen and (max-width: 480px) {
}
@media screen and (max-width: 320px) {
  .footer__container {
    padding: 20px;
    display: block;
  }
  .footer__container-heading {
    width: 100%;
  }
  .footer__container-heading h3 {
    font-size: 25px;
    text-align: center;
  }
  .footer__container-socials {
    margin: 0px;
  }
  .footer__container-navLinks {
    justify-content: space-between;
    width: 100%;
  }
  .footer__container-navLinks a {
    font-size: 12px;
  }

  .footer__container-contactDetails {
    position: static;
    left: 0;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: flex-start;
    margin: 0;
  }
  .footer__container-contactDetails_grid {
    margin: 0;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .footer__contactDetails-img {
    padding: 10px 0;
  }
  .footer__contactDetails-text {
    font-size: 12px;
  }
}
