.teams-container {
  background-color: var(--color-bg);
  margin: 50px;
  padding: 20px;
}
.section__heading {
  width: 100%;
  height: auto;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
  -ms-flex-pack: center;
  justify-content: center;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;

  padding: 20px 0;
  margin: 20px 0;
}

.section__heading h2 {
  font-family: var(--font-family-heading);
  font-size: 128px;
  font-weight: bolder;
  color: var(--color-heading);
}
@media screen and (max-width: 768px) {
  .teams-container {
    margin: 0;
    padding: 0 20px;
  }
}
@media screen and (max-width: 480px) {
  .teams-container {
    margin: 0;
    padding: 0 20px;
  }
}
@media screen and (max-width: 320px) {
  .teams-container {
    margin: 0;
    padding: 0 20px;
  }
}
