.contactText {
}

.contactText__info {
  width: 100%;
  height: auto;
}

.contactText__info h3 {
  font-family: var(--font-family-text);
  font-size: 55px;
  font-weight: bold;
  color: var(--color-heading);
  margin-bottom: 3px;
  height: 66px;
  margin-top: 37px;
}

.contactText__info p {
  font-family: var(--font-family-text);
  font-size: 1rem;
  font-weight: normal;
  color: var(--color-text);
  line-height: 15pt;
  text-align: justify;
}

@media screen and (max-width: 768px) {
  .contactText__info {
    display: block;
    padding: 0;
  }
  .contactText__info h3 {
    font-size: 40px;
    text-align: center;
    margin-bottom: 15px;
    padding: 0px;
  }
  .contactText__info p {
    font-size: 14px;
    line-height: 20pt;
  }
}
@media screen and (max-width: 480px) {
  .contactText__info {
    display: block;
    padding: 0;
  }
  .contactText__info h3 {
    font-size: 25px;
    text-align: center;
    margin-bottom: 15px;
    padding: 0px;
  }
  .contactText__info p {
    font-size: 14px;
    line-height: 20pt;
  }
}
@media screen and (max-width: 320px) {
  .contactText__info {
    display: block;
    padding: 0;
  }
  .contactText__info h3 {
    font-size: 25px;
    text-align: center;
    margin-bottom: 15px;
    padding: 0px;
  }
  .contactText__info p {
    font-size: 14px;
    line-height: 20pt;
  }
}
