.navbar {
  width: 100%;
  background-color: var(--color-bg);
  padding: 10px 0;
  position: relative; /* Important for positioning */
  top: 0;
  z-index: 2;
  -webkit-transition: all 0.3s ease-in-out;
  -o-transition: all 0.3s ease-in-out;
  transition: all 0.3s ease-in-out;
}
.sticky {
  position: fixed;
  top: 0;
  z-index: 2;
  width: 100%;
}

.navbar__container {
  padding: 0 30px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
  -ms-flex-pack: justify;
  justify-content: space-between;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
  color: white;
}

.navbar-logo {
  font-size: 3em;
  font-family: var(--font-family-heading);
  font-weight: bold;
  color: var(--color-heading);
}

.navbar-links {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  gap: 2em;
}

.navbar-links a {
  color: var(--color-heading);
  text-decoration: none;
  font-size: 1.5em;
  font-family: var(--font-family-heading);
  font-weight: bold;
}

.navbar-links a:hover {
  font-size: 2em;
  cursor: pointer;
}

.navbar-menu {
  display: none;
  cursor: pointer;
}

.navbar-menu-container {
  position: absolute;
  top: 60px;
  right: 0;
  background-color: var(--color-footer);
  padding: 1em;
  border-radius: 8px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
  -ms-flex-direction: column;
  flex-direction: column;
  -webkit-box-align: start;
  -ms-flex-align: start;
  align-items: flex-start;
  gap: 1em;
}

.navbar-menu-container a {
  color: white;
  font-family: var(--font-family-heading);
  text-decoration: none;
  font-size: 1em;
}

.navbar-menu-container a:hover {
  text-decoration: none;
  font-size: 1.5em;
  color: var(--color-text);
}

@media (max-width: 768px) {
  .navbar-links {
    display: none;
  }

  .navbar-menu {
    display: block;
  }
}
