.contactHeading {
  margin: 0px;
  padding: 0px;
}

.contactHeading-heading {
  width: 100%;
  height: auto;
}

.contactHeading-heading h2 {
  font-family: var(--font-family-heading);
  font-size: 350px;
  font-weight: bold;
  color: var(--color-heading);
  letter-spacing: 50pt;
  margin: 0;
  text-align: center;
}
@media screen and (max-width: 768px) {
  .contactHeading-heading h2 {
    font-size: 100px;
    letter-spacing: 35pt;
  }
}
@media screen and (max-width: 480px) {
  .contactHeading-heading h2 {
    font-size: 60px;
    letter-spacing: 35px;
  }
}
@media screen and (max-width: 320px) {
  .contactHeading-heading h2 {
    font-size: 40px;
    letter-spacing: 20px;
  }
}
