.project-card {
}
.projectCard__container-even {
  margin: 50px;
  padding: 20px;
  display: -ms-grid;
  display: grid;
  gap: 20px;
  -ms-grid-columns: 1fr 20px 1fr;
  grid-template-columns: 1fr 1fr;
}

.projectCard__container-odd {
  margin: 50px;
  padding: 20px;
  display: -ms-grid;
  display: grid;
  gap: 20px;
  -ms-grid-columns: 1fr 20px 1fr;
  grid-template-columns: 1fr 1fr;
}
.even {
  margin-bottom: 30px;
}

.odd {
  padding: 30px;
}

.projectCard__container-number-even {
  display: -ms-grid;
  display: grid;
  gap: 10px;
  -ms-grid-columns: 2fr 10px 1fr;
  grid-template-columns: 2fr 1fr;
  margin: 10px;
}
.projectCard__container-number-odd {
  display: -ms-grid;
  display: grid;
  gap: 10px;
  -ms-grid-columns: 1fr 10px 2fr;
  grid-template-columns: 1fr 2fr;
  margin: 10px;
}

.line1 {
  margin: 0px;
  padding: 0px;
  width: clamp(30%, 100%, 150%);
  height: auto;
  position: relative;
  left: -43px;
  top: 7px;
}
.line1 img {
  width: 100%;
  height: auto;
}
.line2 {
  margin: 0px;
  padding: 0px;
  width: clamp(30%, 100%, 150%);
  height: auto;
  position: relative;
  left: 6%;
  top: 7px;
}
.line2 img {
  width: 100%;
  height: auto;
}

.projectNumberEven {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: start;
  -ms-flex-pack: start;
  justify-content: flex-start;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
}
.projectNumberEven,
.projectNumberOdd {
  font-family: var(--font-family-text);
  font-size: 34px;
  font-weight: bold;
  color: var(--color-heading);
}
.project-video iframe {
  width: 560px;
  height: 315px;
}

.projectNumberOdd {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: end;
  -ms-flex-pack: end;
  justify-content: flex-end;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
}

.project-details {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
  -ms-flex-direction: column;
  flex-direction: column;
  -ms-flex-pack: distribute;
  justify-content: space-around;
}
.project-details h3 {
  font-family: var(--font-family-text);
  font-size: 24px;
  font-weight: bold;
  color: var(--color-heading);
}
.project-details p {
  font-family: var(--font-family-text);
  font-size: 1rem;
  font-weight: normal;
  color: var(--color-text);
}
#artistHeading {
  font-family: var(--font-family-text);
  font-size: 20px;
  font-weight: 400;
  color: var(--color-heading);
}

.video-thumbnail {
  width: 100%;
  border-radius: 10px;
}
.artist-section {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
  -ms-flex-direction: column;
  flex-direction: column;
}

.artist-link {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
  text-decoration: none;
  color: inherit;
  margin-bottom: 10px;
}

.artist-thumbnail {
  width: 50px;
  height: 50px;
  border-radius: 50%;
  margin-right: 10px;
}

@media screen and (max-width: 768px) {
  .line1,
  .line2 {
    top: 16px;
  }
  .project-video {
    margin: 30px 0;
  }
  .project-video iframe {
    width: 100%;
    height: 432px;
  }
  .projectCard__container-even {
    margin-top: 30px;
    margin: 0;
    padding: 0;
    display: flex;
    flex-direction: column-reverse;
    gap: 20px;
  }
  .projectCard__container-odd {
    margin: 0;
    padding: 0;
    display: block;
  }
  .project-details {
    margin-bottom: 30px;
  }
  .project-details h3,
  .project-details p {
    margin-bottom: 10px;
  }
  .even {
    margin-bottom: 30px;
    padding: 0;
  }
  .odd {
    margin-bottom: 30px;
    padding: 0;
  }
}
@media screen and (max-width: 480px) {
  .projectCard__container-even {
    margin: 0;
    padding: 0;
    display: flex;
    flex-direction: column-reverse;
    gap: 20px;
  }
  .project-video iframe {
    width: 100%;
    height: 270px;
  }

  .projectCard__container-odd {
    margin: 0;
    padding: 0;
    display: block;
  }
  .projectCard__container-number-even {
    display: none;
  }
  .projectCard__container-number-odd {
    display: none;
  }

  .even {
    margin-bottom: 30px;
    padding: 0;
  }
  .odd {
    margin-bottom: 30px;
    padding: 0;
  }
  .project-details {
    margin-bottom: 30px;
  }
  .project-details h3,
  .project-details p {
    margin-bottom: 10px;
  }
}
@media screen and (max-width: 320px) {
  .projectCard__container-even {
    margin: 0;
    padding: 0;
    display: flex;
    flex-direction: column-reverse;
    gap: 20px;
  }
  .project-video iframe {
    width: 100%;
    height: auto;
  }

  .projectCard__container-odd {
    margin: 0;
    padding: 0;
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    gap: 20px;
  }
  .projectCard__container-number-even {
    display: none;
  }
  .projectCard__container-number-odd {
    display: none;
  }

  .even {
    margin-bottom: 30px;
    padding: 0;
  }
  .odd {
    margin-bottom: 30px;
    padding: 0;
  }
  .project-details {
    margin-bottom: 30px;
  }
  .project-details h3,
  .project-details p {
    margin-bottom: 10px;
  }
}
