.home {
  overflow: hidden;
  position: relative;
  width: 100vw;
  height: 100vh; /* Ensure the home div takes up the full viewport height */
  overflow: hidden;

  margin: 0%;
  padding: 0;

  display: -webkit-box;

  display: -ms-flexbox;

  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
  -ms-flex-direction: column;
  flex-direction: column;
  -webkit-box-pack: center;
  -ms-flex-pack: center;
  justify-content: center;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
}

.home__background {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  overflow: hidden;
}
.home__background video {
  position: absolute;
  top: 50%;
  left: 50%;
  width: 100%;
  height: 100%;
  -o-object-fit: cover;
  object-fit: cover; /* Ensures the video covers the entire div */
  -webkit-transform: translate(-50%, -50%);
  -ms-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%); /* Centers the video */
}

.home__h1 {
  position: absolute;
  top: 40%;
  left: 32%;
}
.home__h1 h1 {
  font-family: var(--font-family-heading);
  font-size: 150px;
  font-weight: bold;
  color: var(--color-heading);
  letter-spacing: 15pt;
}

.home__navigation {
  position: absolute;
  top: 65%;
  left: 32%;
}

.home__navigation_container {
}

.home__navigation_container-links > p {
  margin: 2px;
}

.home__navigation_container-links_flex {
  display: -ms-grid;
  display: grid;
  -ms-grid-columns: 1fr 1fr;
  grid-template-columns: 1fr 1fr;
}

.home__navigation_container-links_flex > p {
  margin: 9px 3px;
  font-family: var(--font-family-text);
  font-weight: 300;
  font-size: 1em;
  color: var(--color-text);
  text-align: left;
}

.home__navigation_container-links_flex > a {
  text-decoration: none;
  cursor: pointer;
  margin: 5px 3px;
  font-family: var(--font-family-heading);
  font-size: 1.5rem;
  font-weight: bold;
  color: var(--color-heading);
  font-weight: 400;
  text-align: right;
}

.home__text {
  position: absolute;
  top: 85%;
  left: 26%;
  text-align: center;
  line-height: 1.7;
  width: 50%;
  font-family: var(--font-family-text);
  font-size: 16px;
  font-weight: 400;
  color: var(--color-text);
}

