@media screen and (max-width: 768px) {
  .home__h1 {
    top: 45%;
    left: 17%;
  }
  .home__h1 h1 {
    font-size: 78px;
  }
  .home__navigation {
    top: 60%;
    left: -4%;
  }
  .home__navigation_container-links_flex > a {
    font-size: 25px;
    padding: 0 10px;
  }
  .home__navigation_container-links_flex > p {
    padding: 4px 0;
    font-weight: 400;
    margin: 5px 3px;
  }
  .home__text {
    font-size: 16px;
    font-weight: normal;
    top: 80%;
    left: 17%;
    width: 70%;
  }
}

@media screen and (max-width: 480px) {
  .home__h1 {
    top: 40%;
    left: 0;
  }
  .home__h1 h1 {
    font-size: 100px;
    text-align: center;
  }
  .home__navigation {
    display: none;
  }

  .home__text {
    font-size: 16px;
    font-weight: normal;
    top: 80%;
    left: 0;
    width: 100%;
    padding: 0 20px;
    font-size: 15px;
    text-align: justify;
    display: none;
  }
  .home__background video {
    -o-object-fit: cover;
    object-fit: cover;
  }
}

@media screen and (max-width: 320px) {
  .home__h1 {
    top: 50%;
    left: 0;
  }
  .home__h1 h1 {
    font-size: 60px;
    text-align: center;
  }
}
