* {
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  padding: 0;
  margin: 0;
  scroll-behavior: smooth;
}
body {
  background: var(--color-bg);
  margin: 0;
  padding: 0;
}

html,
body {
  overflow-x: hidden;
  margin: 0;
  padding: 0;
  width: 100%;
  background: var(--color-bg);
}

.app__navbar {
  position: sticky;
  top: 0%;
}
